import { _decorator, Component, Node, AudioClip, AudioSource, Toggle } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Audio')
export class Audio extends Component {
    static instance: Audio;
    @property(AudioClip)
    public clips: AudioClip [] = [];
    @property(AudioSource)
    public audioSource1: AudioSource = null;
    @property(Toggle)
    public btn: Toggle = null;

    public onOffAudio: boolean = true;
    public onOffMusic: boolean = true;
    public clip;
    protected onLoad(): void {
        Audio.instance = this;
    }
    onAudioQueue(index: number){
        let clip: AudioClip = this.clips[index];
        this.audioSource1.playOneShot(clip);
    }

}


